import React from 'react';
import {observer} from "mobx-react";
import {LinkedRadioGroup} from "../FormControls";

@observer
class LobbyForm extends React.Component {
	constructor(props) {
		super(props);
		this.store = props.store.formData.lobbyItems;
	}

	render() {
		return (
			<form>
				<div className="form-row form-group">
					<div className="col-12">
						<h5> :</h5>
						<LinkedRadioGroup
							linkObject={this.store}
							options={[
								{label: '  ', linkKey: 'hooksAndKaloshnitca'},
								{label: ' ', linkKey: 'locker'},
								{label: ' ,  ', linkKey: 'allOptions'},
							]}
						/>
					</div>
				</div>
			</form>)
	}
}

export {LobbyForm}


